import React, { useState, useEffect } from "react";
import axios from "axios";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import MoreHorizIcon from "@material-ui/icons/MoreHoriz";
import QueueIcon from "@material-ui/icons/Queue";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import AddMember from "./AddMember";
import GroupAddIcon from "@material-ui/icons/GroupAdd";
const menuItems = [
  {
    icon: <GroupAddIcon />,
    itemName: "Add member",
    id: 0,
    privacy: true,
  },
  {
    icon: <QueueIcon />,
    itemName: "Leave team",
    id: 1,
    privacy: false,
  },
  {
    icon: <QueueIcon />,
    itemName: "Edit Team",
    id: 2,
    privacy: true,
  },
];

const MenuBox = ({ Team, user }) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [showAddMember, showAddModal] = useState(false);
  const [users, setUsers] = useState([]);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleEvent = (menuItemId) => {
    if (menuItemId === 0) {
      showAddModal((showAddMember) => !showAddMember);
    }
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div>
      <MoreHorizIcon
        onClick={handleClick}
        style={{ height: "20px", width: "20px", cursor: "pointer" }}
      />
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {menuItems.map((menuItem) => {
          if (menuItem.privacy) {
            if (Team.privacy && Team.Owner.email !== user.email) return null;
          }
          return (
            <MenuItem
              style={{ display: "flex" }}
              key={menuItem.id}
              onClick={() => handleEvent(menuItem.id)}
            >
              <ListItemIcon style={{ color: "blue" }}>
                {menuItem.icon}
              </ListItemIcon>
              <ListItemText>{menuItem.itemName}</ListItemText>
            </MenuItem>
          );
        })}
      </Menu>
      <AddMember
        Team={Team}
        show={showAddMember}
        handleEvent={() => handleEvent(0)}
      />
    </div>
  );
};

export default MenuBox;
